h1, h2, h3, h4, h5, h6, p,
var,fieldset, form, label, legend
 {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

:root {
    font-size: 18px;
    --fontstack: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
    height: 115vh;
    font-family: var(--fontstack);
    padding: 40px 0px 0px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    height: 100%;
    width: 90%;
    display: grid;
    grid-gap: 20px;
    grid-template-rows: 130px 90px 1fr 130px;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-title {
    font-size: 2.5rem;
}

.status-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.status-text {
    font-size: 1.5rem;
}
.grid-content {
    width: 90%;
    margin: 0px auto;
    display: grid;
    grid-gap: 90px;
    grid-template-columns: 1fr 1fr;
}

.grid-section-container {
    display: grid;
    grid-gap: 30px;
    grid-template-rows: 40px 1fr;
}

.grid-title {
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.grid {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
}

.grid-square {
    border: 1px solid black;
}

.ship {
    background-color: blue;
}

.hit,
.miss {
    pointer-events: none;
}

.hit {
    background-color: red;
}

.miss {
    background-color: black;
}

.button-container {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-gap: 5px;
    justify-content: center;
    align-items: center;
}

.reset-bttn,
.set-up-bttn {
    width: 100px;
    height: 60px;
    font-size: 1rem;
